body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
  height: 100%;
}

h3 {
  font-weight: bold;
  margin-bottom: 16px;
}

h1 {
  font-size: 200%;
  font-weight: bolder;
  word-spacing: 150%;
  color: teal;
}

#instructions {
  flex-grow: 1;
}


#ingredients {
  width: 25%;
}


section {
  display: flex;
  height: 100%;
}

#search {
  width: 30%;
  margin: 16px;
}

#search > form {
  margin: 8px;
  display: flex;
}

#search > form > input {
  padding: 2px 8px;
  flex-grow: 1;
  margin-right: 6px;
}



ul {
  list-style-type: none;
  }

  .details {
    display: flex;
    height: 100%;
  }

  .details li > p {
    margin: 6px 0px;
  }
  
  .details li {
    font-size: 75%;
 
  }
  
  
section > article {
  flex-grow: 1;
}


#search > form > button {
  color: white;
  width: 50%;
  padding: 2px;
  background-color: teal;
  font-weight: bold;
   border-radius: 10px;
  border: none;
  
}

#search input, #search li {
  border-radius: 10px;
  border: solid 1px;
}

#results {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.card {
  cursor: pointer;
  margin: 0px;
  padding: 8px;
}

.card > p {
  margin: 0;
  font-weight: lighter;
  text-transform: uppercase;
}






